/************************************************* HEADER **************************************************/

nav.navbar {
  background-color: #f9f9f9;
  padding-top: 21px;
  padding-bottom: 21px;
  border-bottom: 2px solid #dedede;

  .navbar-brand {
    transition: opacity .3s;

    &:hover {
      opacity: 0.7;
    }
  }

  .nav-item .nav-link {
    padding: 10px 15px;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 0.5px;
    font-weight: 800;
    color: #606060;
    text-transform: uppercase;
    transition: all .3s;
    border-radius: 3px;

    &:hover {
      color: #fff;
      background-color: #00a99d;
    }
  }

  .nav-item.dropdown {
    .nav-link::after {
      display: none;
    }

    .dropdown-menu {
      padding: 0;
      left: auto;
      right: 100%;
      top: 0;
      margin: 0;

      &:focus-within {
        border-color: rgba(0, 0, 0, 0.8);
      }

      .form-inline {
        flex-flow: row;

        .form-control {
          margin: 0;
          border: none;

          &:hover,
          &:focus {
            box-shadow: none;
          }
        }

        .btn {
          &:hover {
            background-color: #00a99d;
            color: #fff;
          }

          &:active,
          &:focus {
            box-shadow: none;
          }
        }
      }
    }
  }
}

.navbar.fixed {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  padding: 0;
  transition: all .5s;
}


/****************************************************** MAIN **************************************************************/

/************ section services *********************/

.services {
  padding-top: 80px;
  padding-bottom: 115px;
  background-color: #f9f9f9;

  &__item {

    span {
      width: 100px;
      background-color: #383838;
      text-align: center;
      border-radius: 50%;
      margin-bottom: 30px;
      transition: all .3s;

      i {
        font-size: 36px;
        line-height: 100px;
        color: #e4e4e4;
      }
    }

    h4 {
      font-size: 14px;
      line-height: 14px;
      font-weight: 800;
      text-transform: uppercase;
      color: #606060;
      margin-bottom: 15px;
      text-align: center;
    }

    p {
      margin-bottom: 20px;
      font-size: 14px;
      font-weight: 500;
      line-height: 24px;
      color: #7a7a7a;
      text-align: center;
    }

    button {
      font-size: 10px;
      line-height: 10px;
      letter-spacing: 0;
      font-weight: 800;
      color: #8a8a8a;
      text-transform: uppercase;
      padding: 10px;
      border: 1px solid #dedede;
      background-color: #f9f9f9;
      border-radius: 4px;
      transition: all .3s;
    }

    &:hover {

      span,
      button {
        background-color: #00a99d;
        color: #e4e4e4;
      }
    }
  }
}

.title {
  margin-bottom: 50px;

  &__h {
    margin-bottom: 15px;

    h2 {
      font-size: 26px;
      line-height: 20px;
      font-weight: 800;
      text-transform: uppercase;
      padding: 0 20px;
      text-align: center;

      .blue {
        color: #00a99d;
      }

      &.black {
        color: #383838;
      }

      &.grey {
        color: #e4e4e4;
      }
    }

    &_line {
      background-image: url(../img/line.png);
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  p {
    font-size: 16px;
    line-height: 16px;
    text-align: center;
    color: #8a8a8a;
  }
}


.romb {
  position: absolute;
  bottom: 0;
  left: calc(50% - 27px);
  z-index: 5;

  &__icon {
    width: 0;
    height: 0;
    border: 27px solid transparent;
    border-bottom-color: #00a99d;
    position: relative;

    &:after {
      content: "";
      width: 0;
      height: 0;
      position: absolute;
      left: -27px;
      top: 27px;
      border: 27px solid transparent;
      border-top-color: #00a99d;
    }

    &:before {
      font-size: 22px;
      color: #e4e4e4;
      position: absolute;
      left: -11px;
      top: 10px;
      z-index: 5;
    }

    &_key::before {
      font-family: Fontawesome;
      content: "\f084";
      transform: rotate(-90deg)
    }

    &_box::before {
      font-family: Fontawesome;
      content: "\f187";
    }

    &_quotes {
      border-bottom-color: #383838;

      &::after {
        border-top-color: #383838;
      }

      &::before {
        font-family: Fontawesome;
        content: "\f10d";
      }
    }

    &_up::before {
      font-family: Fontawesome;
      content: "\f0d8";
      left: -7px;
    }
  }
}

.ipad {
  position: absolute;
  left: 0;
  top: 25px;
}

