.flag {
  width: 40px;
  min-width: 40px;
  height: 55px;
  background-color: #00a99d;
  text-align: center;
  position: relative;
  z-index: 1;
  transition: all .3s;
  margin-right: 20px;

  i {
    position: relative;
    font-size: 20px;
    line-height: 48px;
    color: #e4e4e4;
    z-index: 4;
    width: 100%;

    &:after {
      content: "";
      width: 0;
      height: 0;
      position: absolute;
      left: 0px;
      top: 36px;
      border: 20px solid transparent;
      border-top-color: #404040;
      border-top-width: 14px;
      z-index: 2;
      transition: all .3s;
    }
  }

  &:before {
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    left: 0px;
    top: 55px;
    border: 20px solid transparent;
    border-top-color: #00a99d;
    border-top-width: 14px;
    z-index: 1;
    transition: all .3s;
  }

  &:after {
    content: "";
    width: 100%;
    height: 0;
    position: absolute;
    top: 0;
    left: 0px;
    height: 36px;
    background-color: #404040;
    z-index: 3;
    transition: all .3s;
  }
}

