      }
    }
  }
}


/*************************** section.team **************************/

.team {
  padding: 70px 0;
  background-color: #f9f9f9;

  .member {
    border-radius: 5px;

    &:hover {

      .member__photo::before {
        opacity: 0.8;
      }

      .member__links {
        display: flex;
      }

      .member__name {
        background-color: #383838;
      }
    }

    &__photo {
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        width: 100%;
        background-color: #00a99d;
        opacity: 0;
        transition: all .3s;
      }
    }

    &__links {
      position: absolute;
      top: -20px;
      bottom: 0;
      width: 100%;
      display: none;
      align-items: center;
      justify-content: center;
      transition: all .3s;
    }

    &__title {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      padding-top: 25px;
      padding-bottom: 10px;
      padding-left: 15px;
      padding-right: 15px;
      border: 1px solid #dedede;
      border-bottom-left-radius: 3px;
      border-bottom-right-radius: 3px;
    }

    &__name {
      position: absolute;
      top: -20px;
      left: calc(50% - 64px);
      display: inline-block;
      font-size: 12px;
      font-weight: 800;
      line-height: 40px;
      padding: 0 15px;
      text-transform: uppercase;
      color: #e4e4e4;
      background-color: #00a99d;
      border-radius: 3px;
      transition: all .3s;
    }

    &__spec {
      font-size: 12px;
      line-height: 22px;
      font-weight: 500;
      color: #606060;
      position: relative;

      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 1px;
        background-color: #ebebeb;
        bottom: -8px;
        left: 0;
      }
    }

    &__contacts {
      font-size: 12px;
      line-height: 32px;
      color: #aaa;

      &:hover {
        text-decoration: none;
        color: #00a99d;
      }

      &:first-of-type {
        margin-top: 10px;
      }

      i {
        margin-right: 10px;
        font-size: 14px;
      }
    }
  }

  .romb_20 {
    position: relative;

    &:nth-of-type(2),
    &:nth-of-type(3),
    &:nth-of-type(4),
    &:nth-of-type(5) {
      margin-left: -10px;
    }

    &__icon {
      width: 0;
      height: 0;
      border: 20px solid transparent;
      border-bottom-color: #e4e4e4;
      position: relative;
      z-index: 5;
      transition: all .3s;

      &:after {
        content: "";
        width: 0;
        height: 0;
        position: absolute;
        left: -20px;
        top: 20px;
        border: 20px solid transparent;
        border-top-color: #e4e4e4;
        transition: all .3s;
      }

      &:before {
        font-size: 14px;
        color: #00a99d;
        position: absolute;
        width: 100%;
        text-align: center;
        top: 9px;
        z-index: 5;
        transition: all .3s;
      }

      &:hover {
        z-index: 6;
        border-bottom-color: #383838;

        &::after {
          border-top-color: #383838;
        }

        &::before {
          color: #e4e4e4;
        }
      }

      &_fb {

        &::before {
          left: -4px;
          font-family: Fontawesome;
          content: "\f09a";
        }
      }

      &_tw {

        &::before {
          font-family: Fontawesome;
          content: "\f099";
        }
      }

      &_google {

        &::before {
          font-family: Fontawesome;
          content: "\f0d5";
        }
      }

      &_vimeo {

        &::before {
          font-family: Fontawesome;
          content: "\f27d";
        }
      }

      &_dribble {
        &::before {
          left: -5px;
          font-family: Fontawesome;
          content: "\f17d";
        }
      }
    }
  }

  .owl-carousel {
    .owl-nav {
      position: absolute;
      top: -105px;
      display: flex;
      justify-content: space-between;
      width: 100%;

      button {
        font-size: 20px;
        line-height: 30px;
        width: 30px;
        background-color: #00a99d;
        border-radius: 2px;
        color: #e4e4e4;
        outline: none;
        transition: .15s;

        &:hover {
          background-color: #383838;
        }
      }
    }
  }
}


/*************************** section.quotes **************************/

