/*************************** section.clients **************************/

.clients {
  padding: 50px 0;
  background-color: #383838;

  &__item {
    background-color: #333333;
    height: 160px;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: .3;
    transition: .15s;
    cursor: pointer;

    &:hover {
      opacity: 1;
    }

    img {
      object-fit: scale-down;
    }
  }

  .owl-carousel {
    .owl-nav {
      display: flex;
      justify-content: space-between;
      position: absolute;
      top: calc(50% - 15px);
      left: -60px;
      width: calc(100% + 120px);

      button {
        font-size: 20px;
        line-height: 30px;
        width: 30px;
        background-color: #00a99d;
        border-radius: 2px;
        color: #e4e4e4;
        outline: none;
        transition: .15s;
        margin: 0 5px;

        &:hover {
          background-color: #e4e4e4;
          color: #00a99d;
        }
      }
    }
  }
}



