    border: 6px solid transparent;
    border-bottom-color: #383838;
    position: relative;
    z-index: 5;
    transition: all .3s;

    &:after {
      content: "";
      width: 0;
      height: 0;
      position: absolute;
      left: -6px;
      top: 6px;
      border: 6px solid transparent;
      border-top-color: #383838;
      transition: all .3s;
    }
  }

  .plan {
    padding: 45px 15px 25px;
    border: 1px solid #dedede;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 270px;

    &:hover {
      .plan__name {
        background-color: #00a99d;

        i {
          color: #e4e4e4;
        }
      }

      .plan__price {

        &::before,
        &::after {
          color: #00a99d;
        }
      }
    }

    &__name {
      position: absolute;
      top: -20px;
      font-size: 12px;
      font-weight: 800;
      line-height: 40px;
      color: #e4e4e4;
      text-transform: uppercase;
      background-color: #383838;
      padding: 0 15px;
      border-radius: 3px;
      transition: .3s;

      i {
        font-size: 14px;
        color: #00a99d;
        margin-left: 10px;
        transition: .3s;

        &:first-of-type {
          margin-left: 0;
          margin-right: 10px;
        }
      }
    }

    &__price {
      position: relative;
      font-size: 60px;
      font-weight: 800;
      line-height: 60px;
      margin-bottom: 10px;

      &::before {
        content: '$';
        font-size: 16px;
        font-weight: 50;
        color: #8a8a8a;
        margin-right: 5px;
      }

      &::after {
        content: 'Monthly';
        font-size: 12px;
        font-weight: 500;
        color: #8a8a8a;
        margin-left: 10px;
      }

      &_cent {
        font-size: 16px;
        line-height: 22px;
        position: absolute;
        top: 0;
      }
    }

    &__prop {
      width: 100%;
      text-align: center;
      border-top: 1px solid #ebebeb;
      border-bottom: 1px solid #ebebeb;
      padding-top: 25px;
      margin-bottom: 25px;

      li {
        margin-bottom: 25px;
        font-size: 14px;
        line-height: 16px;
        color: #8a8a8a;

        &:nth-last-of-type() {
          margin-bottom: 0;
        }
      }
    }

    &__btn {
      font-size: 12px;
      font-weight: 800;
      line-height: 40px;
      background-color: #00a99d;
      padding: 0 15px;
      border: none;
      text-transform: uppercase;
      color: #e4e4e4;
      border-radius: 3px;
      transition: .15s;

      &:hover {
        background-color: #383838;
        color: #00a99d;
      }
    }
  }
}

/*************************** aside.purchase **************************/

.purchase {
  padding: 50px 0;
  background-color: #8a8a8a;

  &__title {
    font-size: 22px;
    color: #383838;
    font-weight: 800;

    i {
      color: #00a99d;
    }
  }

  &__btn {
    font-size: 12px;
    color: #e4e4e4;
    font-weight: 800;
    text-transform: uppercase;
    line-height: 40px;
    background-color: #00a99d;
    padding: 0 15px;
    border: none;
    border-radius: 3px;
    transition: .15s;

    i {
      font-size: 14px;
    }

    &:hover {
      background-color: #383838;
    }
  }
}

/*************************** section.social **************************/

.social {
  background-color: #383838;
  padding: 50px 0;

  &__logo {
    transition: .3s;

    &:hover {
      transform: scale(1.2);
    }
  }

  &__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 65px;
  }

  &__link {
    line-height: 40px;
    width: 40px;
    font-size: 20px;
    color: #e4e4e4;
    border: 1px solid #606060;
    border-radius: 3px;
    text-align: center;
    transition: .15s;
    margin-left: 10px;

    &:first-of-type {
      margin-left: 0;
    }

    &:hover {
      background-color: #00a99d;
      color: #e4e4e4;
      border: none;
    }
  }
}

/*************************** section.write **************************/



.write {
  padding: 80px 0;

  &__form {
    padding: 20px;
    display: flex;
    flex-direction: column;
    border: 1px solid #dedede;
    border-radius: 3px;
    width: 100%;
    max-width: 370px;
    position: relative;
    z-index: 2;
    background-color: #f9f9f9;
  }

  &__name,
  &__email,
  &__message {
    padding: 10px 15px;
    border: 1px solid #dedede;
    border-radius: 3px;
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 20px;

    &:focus {
      outline: none;
      border-color: #aaa;

    }

    &:placeholder-shown {
      font-style: italic;
    }
  }

  &__btn {
    width: 100%;
    border: none;
    font-size: 12px;
    font-weight: 800;
    line-height: 40px;
    text-transform: uppercase;
    background-color: #00a99d;
    border-radius: 3px;
    color: #e4e4e4;
    transition: .15s;

    &:hover {
      background-color: #383838;
    }
  }

  .map {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    z-index: 1;
  }
}

/*************************** section.copyright **************************/

.copyright {
  padding: 50px 0 25px;
  background-color: #383838;

  &__text {
    font-size: 14px;
    font-weight: 800;
    color: #e4e4e4;
    text-align: center;
  }
}







