/**********************
* Base style
**********************/
html,
body {
  height: 100%;
}

body {
  background: #fff;
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  color: #000;
  font-weight: 400;
  min-width: 320px;
}

img {
  max-width: 100%;
  height: auto;
}

section {
  position: relative;
}

ul {
  list-style: none;
  padding: 0;
}

br {
  font-size: 0;
}

/**********************
* typography
**********************/
h1,
h2,
h3,
h4,
h5,
h6,
p,
hr {
  margin: 0;
  font-weight: 400;
}

a {
  display: inline-block;
}

/************************************************* HEADER **************************************************/
nav.navbar {
  background-color: #f9f9f9;
  padding-top: 21px;
  padding-bottom: 21px;
  border-bottom: 2px solid #dedede;
}

nav.navbar .navbar-brand {
  transition: opacity .3s;
}

nav.navbar .navbar-brand:hover {
  opacity: 0.7;
}

nav.navbar .nav-item .nav-link {
  padding: 10px 15px;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 0.5px;
  font-weight: 800;
  color: #606060;
  text-transform: uppercase;
  transition: all .3s;
  border-radius: 3px;
}

nav.navbar .nav-item .nav-link:hover {
  color: #fff;
  background-color: #00a99d;
}

nav.navbar .nav-item.dropdown .nav-link::after {
  display: none;
}

nav.navbar .nav-item.dropdown .dropdown-menu {
  padding: 0;
  left: auto;
  right: 100%;
  top: 0;
  margin: 0;
}

nav.navbar .nav-item.dropdown .dropdown-menu:focus-within {
  border-color: rgba(0, 0, 0, 0.8);
}

nav.navbar .nav-item.dropdown .dropdown-menu .form-inline {
  flex-flow: row;
}

nav.navbar .nav-item.dropdown .dropdown-menu .form-inline .form-control {
  margin: 0;
  border: none;
}

nav.navbar .nav-item.dropdown .dropdown-menu .form-inline .form-control:hover, nav.navbar .nav-item.dropdown .dropdown-menu .form-inline .form-control:focus {
  box-shadow: none;
}

nav.navbar .nav-item.dropdown .dropdown-menu .form-inline .btn:hover {
  background-color: #00a99d;
  color: #fff;
}

nav.navbar .nav-item.dropdown .dropdown-menu .form-inline .btn:active, nav.navbar .nav-item.dropdown .dropdown-menu .form-inline .btn:focus {
  box-shadow: none;
}

.navbar.fixed {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  padding: 0;
  transition: all .5s;
}

/****************************************************** MAIN **************************************************************/
/************ section services *********************/
.services {
  padding-top: 80px;
  padding-bottom: 115px;
  background-color: #f9f9f9;
}

.services__item span {
  width: 100px;
  background-color: #383838;
  text-align: center;
  border-radius: 50%;
  margin-bottom: 30px;
  transition: all .3s;
}

.services__item span i {
  font-size: 36px;
  line-height: 100px;
  color: #e4e4e4;
}

.services__item h4 {
  font-size: 14px;
  line-height: 14px;
  font-weight: 800;
  text-transform: uppercase;
  color: #606060;
  margin-bottom: 15px;
  text-align: center;
}

.services__item p {
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  color: #7a7a7a;
  text-align: center;
}

.services__item button {
  font-size: 10px;
  line-height: 10px;
  letter-spacing: 0;
  font-weight: 800;
  color: #8a8a8a;
  text-transform: uppercase;
  padding: 10px;
  border: 1px solid #dedede;
  background-color: #f9f9f9;
  border-radius: 4px;
  transition: all .3s;
}

.services__item:hover span,
.services__item:hover button {
  background-color: #00a99d;
  color: #e4e4e4;
}

.title {
  margin-bottom: 50px;
}

.title__h {
  margin-bottom: 15px;
}

.title__h h2 {
  font-size: 26px;
  line-height: 20px;
  font-weight: 800;
  text-transform: uppercase;
  padding: 0 20px;
  text-align: center;
}

.title__h h2 .blue {
  color: #00a99d;
}

.title__h h2.black {
  color: #383838;
}

.title__h h2.grey {
  color: #e4e4e4;
}

.title__h_line {
  background-image: url(../img/line.png);
  background-repeat: no-repeat;
  background-position: center;
}

.title p {
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  color: #8a8a8a;
}

.romb {
  position: absolute;
  bottom: 0;
  left: calc(50% - 27px);
  z-index: 5;
}

.romb__icon {
  width: 0;
  height: 0;
  border: 27px solid transparent;
  border-bottom-color: #00a99d;
  position: relative;
}

.romb__icon:after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  left: -27px;
  top: 27px;
  border: 27px solid transparent;
  border-top-color: #00a99d;
}

.romb__icon:before {
  font-size: 22px;
  color: #e4e4e4;
  position: absolute;
  left: -11px;
  top: 10px;
  z-index: 5;
}

.romb__icon_key::before {
  font-family: Fontawesome;
  content: "\f084";
  transform: rotate(-90deg);
}

.romb__icon_box::before {
  font-family: Fontawesome;
  content: "\f187";
}

.romb__icon_quotes {
  border-bottom-color: #383838;
}

.romb__icon_quotes::after {
  border-top-color: #383838;
}

.romb__icon_quotes::before {
  font-family: Fontawesome;
  content: "\f10d";
}

.romb__icon_up::before {
  font-family: Fontawesome;
  content: "\f0d8";
  left: -7px;
}

.ipad {
  position: absolute;
  left: 0;
  top: 25px;
}

/*************************** section.features **************************/
.features {
  padding-top: 80px;
  background-color: #383838;
  overflow: hidden;
  padding-bottom: 50px;
}

.features__list {
  margin-top: 40px;
}

.features__item {
  margin-bottom: 65px;
}

.features__item:last-of-type {
  margin-bottom: 0;
}

.features__item i {
  display: inline-block;
  width: 87px;
  padding-top: 5px;
  margin-right: 30px;
  text-align: center;
  font-size: 36px;
  color: #00a99d;
}

.features__topic h4 {
  margin-bottom: 15px;
  font-size: 14px;
  line-height: 14px;
  font-weight: 800;
  color: #e4e4e4;
  text-transform: uppercase;
}

.features__topic p {
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
  color: #7a7a7a;
}

.features_bottom {
  padding-top: 75px;
  background-color: #f9f9f9;
}

.table__item {
  display: flex;
  margin-bottom: 55px;
}

.table__item:hover .flag {
  background-color: #404040;
}

.table__item:hover .flag::before {
  border-top-color: #404040;
}

.table__item:hover .flag::after {
  background-color: #00a99d;
}

.table__item:hover .flag i::after {
  border-top-color: #00a99d;
}

.table__item .table__topic h4 {
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 14px;
  font-weight: 800;
  color: #606060;
  text-transform: uppercase;
}

.table__item .table__topic h6 {
  font-size: 12px;
  font-weight: 500;
  color: #aaaaaa;
  margin-bottom: 15px;
}

.table__item .table__topic p {
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
  color: #7a7a7a;
}

.flag {
  width: 40px;
  min-width: 40px;
  height: 55px;
  background-color: #00a99d;
  text-align: center;
  position: relative;
  z-index: 1;
  transition: all .3s;
  margin-right: 20px;
}

.flag i {
  position: relative;
  font-size: 20px;
  line-height: 48px;
  color: #e4e4e4;
  z-index: 4;
  width: 100%;
}

.flag i:after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  left: 0px;
  top: 36px;
  border: 20px solid transparent;
  border-top-color: #404040;
  border-top-width: 14px;
  z-index: 2;
  transition: all .3s;
}

.flag:before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  left: 0px;
  top: 55px;
  border: 20px solid transparent;
  border-top-color: #00a99d;
  border-top-width: 14px;
  z-index: 1;
  transition: all .3s;
}

.flag:after {
  content: "";
  width: 100%;
  height: 0;
  position: absolute;
  top: 0;
  left: 0px;
  height: 36px;
  background-color: #404040;
  z-index: 3;
  transition: all .3s;
}

/*************************** section.skills **************************/
.skills {
  padding-top: 70px;
  padding-bottom: 70px;
  background-color: #383838;
}

.skills__item {
  text-align: center;
}

.skills__item .circle {
  width: 120px;
  height: 120px;
  position: relative;
}

.skills__item .circle span {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  color: #ebebeb;
  font-size: 16px;
}

.skills__item .circle strong {
  font-size: 28px;
  line-height: 28px;
}

.skills__item h4 {
  font-size: 14px;
  line-height: 16px;
  font-weight: 800;
  margin-top: 15px;
  color: #8a8a8a;
  text-transform: uppercase;
}

/*************************** section.works **************************/
.works {
  padding-top: 70px;
  padding-bottom: 70px;
  background-color: #f9f9f9;
}

.works__gallery {
  padding-left: 3px;
  padding-right: 3px;
  margin-bottom: 50px;
}

.works__title {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 169, 157, 0.8);
  color: #e4e4e4;
  transition: all .3s;
  opacity: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.works__title h5 {
  font-size: 16px;
  font-weight: 800;
}

.works__title p {
  font-size: 12px;
  line-height: 22px;
  font-weight: 500;
}

.works__title span {
  font-size: 12px;
  line-height: 22px;
  font-weight: 500;
}

.works__links {
  justify-content: center;
  margin-bottom: 40px;
}

.works__item {
  padding: 1px;
}

.works__item:hover .works__title {
  opacity: 1;
}

.works__all {
  display: block;
  max-width: 1140px;
  width: 100%;
  font-size: 13px;
  text-transform: uppercase;
  background-color: #00a99d;
  border-radius: 3px;
  border: none;
  line-height: 40px;
  font-weight: 800;
  color: #e4e4e4;
  padding: 0 15px;
  transition: all .4s;
}

.works__all:hover {
  color: #00a99d;
  background-color: #e4e4e4;
}

.works__all:focus, .works__all:active {
  outline: none;
}

.romb_20 {
  position: relative;
}

.romb_20__icon {
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-bottom-color: #383838;
  position: relative;
  z-index: 5;
  transition: all .3s;
}

.romb_20__icon:after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  left: -20px;
  top: 20px;
  border: 20px solid transparent;
  border-top-color: #383838;
  transition: all .3s;
}

.romb_20__icon:before {
  font-size: 14px;
  color: #e4e4e4;
  position: absolute;
  left: -7px;
  top: 9px;
  z-index: 5;
  transition: all .3s;
}

.romb_20__icon_link:hover {
  z-index: 6;
  border-bottom-color: #e4e4e4;
}

.romb_20__icon_link:hover::after {
  border-top-color: #e4e4e4;
}

.romb_20__icon_link:hover::before {
  color: #00a99d;
}

.romb_20__icon_link::before {
  font-family: Fontawesome;
  content: "\f0c1";
}

.romb_20__icon_zoom {
  margin-left: -10px;
}

.romb_20__icon_zoom:hover {
  z-index: 6;
  border-bottom-color: #e4e4e4;
}

.romb_20__icon_zoom:hover::after {
  border-top-color: #e4e4e4;
}

.romb_20__icon_zoom:hover::before {
  color: #00a99d;
}

.romb_20__icon_zoom::before {
  font-family: Fontawesome;
  content: "\f002";
}

/*************************** section.facts **************************/
.facts {
  padding-top: 70px;
  padding-bottom: 70px;
  background-color: #383838;
}

.facts__item {
  display: flex;
  flex-direction: column;
}

.facts__item:hover .romb_7 {
  border-bottom-color: #00a99d;
}

.facts__item:hover .romb_7::after {
  border-top-color: #00a99d;
}

.facts__item:hover h5 {
  color: #00a99d;
}

.facts__item h5 {
  font-size: 14px;
  line-height: 16px;
  font-weight: 800;
  color: #606060;
  text-align: center;
  text-transform: uppercase;
  transition: all .3s;
}

.facts__item h5 span {
  font-size: 10px;
}

.facts__number {
  position: relative;
  width: 100%;
  font-size: 80px;
  font-style: italic;
  font-weight: 500;
  color: #e4e4e4;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #484848;
  margin-bottom: 20px;
}

.facts__number::after {
