/**********************
* MEDIA
**********************/

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
  .clients .owl-carousel {
    padding: 0 40px;
  }

  .clients .owl-carousel .owl-nav {
    left: 0;
    width: 100%;
  }

  .features .ipad {
    top: 165px;
  }
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .services,
  .write {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  .features,
  .features_bottom {
    padding-top: 70px;
  }

  .features__item {
    margin-bottom: 50px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  .team .owl-carousel .owl-nav {
    top: -120px;
  }

  header .navbar {
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .skills__item {
    margin-bottom: 40px;
  }


  .social__list {
    margin-top: 50px;
  }

  .write {
    background-color: #f9f9f9;

    .map {
      position: relative;
      width: 100%;
    }
  }

  .features .ipad {
    top: 230px;
  }


}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {

  .features__list {
    margin-top: 0;
  }

  .features__item {
    margin-bottom: 30px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  .title {
    margin-bottom: 30px;
  }

  .services,
  .features,
  .skills,
  .works,
  .facts,
  .team,
  .quotes,
  .clients,
  .prices,
  .write {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .works__gallery {
    margin-bottom: 30px;
  }

  .features_bottom {
    padding-top: 50px;

    .table__item {
      margin-bottom: 50px;
    }
  }

  header .navbar {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  .navbar .nav-item.dropdown .form-control {
    width: 100%;
  }

  .skills__item {
    margin-bottom: 30px;
  }

  .social__link {
    margin-bottom: 10px;
  }

  .facts .title {
    margin-bottom: 15px;
  }

  .social__list {
    margin-top: 40px;
  }

  .copyright {
    padding-top: 40px;
    padding-bottom: 15px;
  }

  .write .map {
    height: 300px;
  }

}

/* Extra Small Devices, Phones */
@media only screen and (max-width : 600px) {




  .team__row {
    padding: 0 15px;
  }

  .team .owl-carousel .owl-nav {
    top: -35px;
  }

}



/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {

  .features_bottom {
    .table__item {
      margin-bottom: 30px;

      &:last-of-type {
        margin-bottom: 0px;
      }
    }
  }

  .services,
  .features,
  .features_bottom,
  .skills,
  .works,
  .facts,
  .team,
  .quotes,
  .clients,
  .prices,
  .purchase,
  .social,
  .write {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .title__h .grey {
    line-height: 26px;
  }


  .facts .title {
    margin-bottom: 0px;
  }

  .social__list {
    margin-top: 30px;
  }
}
